@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.card {
  grid-column-end: span 4;
  display: flex;
  flex-direction: column;
  background-color: #39393b;
  border-radius: 5px;
  transition: all 0.3s ease 0s;
}

.card:hover {
  transform: translateY(-7px);
}
.text--medium {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #ecf0f1;
}

.card__image-container {
  width: 100%;
  padding-top: 56.25%;
  overflow: hidden;
  position: relative;
  border-radius: 5px;
}

.card__image-container img {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.card__content {
  padding: 20px;
}

.card__title {
  margin-bottom: 20px;
}

.card__info {
  display: flex;
  align-self: end;
  align-items: center;
}

.card__price {
  margin-left: auto;
  padding: 5px 20px;
  background-color: #ce1e1e;
  border-radius: 20px;
  border: 0;
  font-size: 18px;
}
.card__price:hover{
  cursor: pointer;
  background-color: #b12828;
  transform: scale(1.03);
}
.name{
  color: #f0f0f0;
  font-size: 19px;
}
ul{
  list-style: none;
  letter-spacing: 0.5px;
}
.specie{
  color: #f0f0f0;
  font-size: 15px;
}
small{
  color: #f0f0f0;
}
.Dead{
  color: rgb(189, 16, 16);
}
.Alive{
  color: rgb(10, 161, 10);
}
.unknown{
  color: rgb(8, 62, 211);
}

@media only screen and (max-width: 1000px) {
  .card {
    grid-column-end: span 6;
  }
}

@media only screen and (max-width: 700px) {
  main {
    grid-gap: 20px;
  }
  .card {
    grid-column-end: span 12;
  }
}

@media only screen and (max-width: 500px) {
  main {
    grid-template-columns: 10px repeat(6, 1fr) 10px;
    grid-gap: 10px;
  }
  .cards {
    grid-column: 2 / span 6;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 20px;
  }
  .card {
    grid-column-end: span 6;
  }
}

.container {
    margin: 5%;
  }
  .cards {
    grid-column: 2 / span 12;
    display: grid;
    grid-template-columns: repeat(12, minmax(auto, 60px));
    grid-gap: 40px;
    font-family: 'Roboto', sans-serif;
  }
  .spinner{
    margin: auto;
    border: 4px solid rgba(0, 0, 0, .1);
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border-left-color: #09f;
    -webkit-animation: spin 1s ease infinite;
            animation: spin 1s ease infinite;
  }
  @-webkit-keyframes spin{
    0%{
      transform: rotate(0deg);
    }
    100%{
      transform: rotate(360deg);
    }
  }
  @keyframes spin{
    0%{
      transform: rotate(0deg);
    }
    100%{
      transform: rotate(360deg);
    }
  }
  .center{
    text-align: center;
    width: 100%;
    display: inline;
    position: absolute;
    left: 0px;
    font-family: 'Roboto', sans-serif;
  }

  @media only screen and (max-width: 920px) and (min-width: 580px) {
    .cards {
      grid-column: 1 / span 14;
      display: grid;
      grid-template-columns: repeat(1, minmax(auto, 60px));
      grid-gap: 40px;
      font-family: 'Roboto', sans-serif;
    }
  }

  @media only screen and (max-width: 580px) {
    .cards {
      grid-column: 1 / span 9;
      display: grid;
      grid-template-columns: repeat(1, minmax(auto, 60px));
      grid-gap: 40px;
      margin-left: 20px;
      font-family: 'Roboto', sans-serif;
    }
  }
body{
    background: rgb(2,0,36);
background: linear-gradient(90deg, rgba(2,0,36,1) 1%, rgba(121,14,40,1) 75%, rgba(144,18,18,1) 100%, rgba(189,9,40,1) 100%);
}
.title{
    text-align: center;
}
.title img{
    width: 300px;
}
.main{
    display: grid;
    grid-template-columns: 1fr repeat(12, minmax(auto, 60px)) 1fr;
    grid-gap: 40px;
    padding: 60px 0;
}
.input{
    text-align: center;
}
.input input{
    padding: 12px;
    outline: none;
    width: 200px;
    border-radius: 5px;
}

